@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light+Two&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@100;300;400;500;600;700;800;900&display=swap);

.brand-logo{font-family:'Big Shoulders Display', sans-serif}.App{background-color:#323031;min-height:100vh;color:#fff}.container__title{font-size:18px;text-align:center;padding:20px 0;color:#9a8c98}.container__info{color:#48c78e;font-size:1rem}.container__info:hover{text-shadow:2px 2px 20px #48c78e}.py1{padding:10px 0}.py3{padding:30px 0}.box-shadow{box-shadow:2px 2px 20px #48c78e}.text-shadow{text-shadow:2px 2px 20px #48c78e}.select__box{display:flex;justify-content:left;padding:20px 0}.ml{margin:10px 0}.active{background:rgba(46,49,49,0.1)}

*{box-sizing:border-box}body{margin:0}ul,ol{margin:0;padding:0;list-style:none}button{border:0}

.char__list{display:flex;flex-flow:row wrap;justify-content:center;margin:0 auto}.card-box{cursor:pointer}.card-box__name{font-family:"IBM Plex Sans", sans-serif;font-weight:100;text-transform:uppercase;font-size:16px}.card-box__text{color:#ffc107;text-shadow:2px 2px 10px #48c78e}figure{margin:0}figure img{width:100%;opacity:0.8;transition:all 0.4s ease-in-out}figure img:hover{opacity:1;box-shadow:2px 2px 30px #c1bd91}.column{box-sizing:border-box;flex:1 1 100%;justify-content:space-between;margin:10px}.card-style{border-image-slice:1;box-shadow:0 1px 2px 0 rgba(0,0,0,0.4);transition:all 0.25s linear}.card-style:hover{box-shadow:2px 2px 20px #48c78e}@media (min-width: 500px){.column{flex-grow:0;flex-shrink:0;flex-basis:calc(100%/2 - 40px)}}@media (min-width: 900px){.column{flex-grow:0;flex-shrink:0;flex-basis:calc(100%/3 - 40px)}}

hr{margin:10px 0}.modalw{height:100vh;width:100vw;background-color:rgba(0,0,0,0.7);position:fixed;z-index:100;top:0;left:0;display:flex;align-items:center;justify-content:center;color:#000}.modal__content{padding:20px;border-radius:4px;background-color:rgba(255,255,255,0.6);width:420px}.intro{display:flex;flex-direction:column;cursor:pointer}.intro__img{width:200px;height:200px;margin:0 auto;border-radius:50%}.intro__title{display:flex;justify-content:space-between;margin:4px}.intro__content{font-size:18px;padding:10px 0}.intro__name{text-align:center;font-size:22px}.intro__title{font-size:16px}

.char-select{background-color:#9a8c98;width:140px;height:40px;padding:4px;outline:none;transition:all ease-in-out 0.2s;display:block}.char-select:active,.char-select:hover{box-shadow:2px 2px 20px #48c78e;background-color:#ffe8d6;display:block}

.button{display:inline-flex;padding:8px;overflow:hidden;cursor:pointer;margin:0;position:relative;background:rgba(0,0,0,0);border:none;border-radius:10px;color:#888;font-weight:600;text-decoration:none;transition:250ms cubic-bezier(0.73, 0.09, 0.21, 0.96) background-color,250ms cubic-bezier(0.73, 0.09, 0.21, 0.96) border-color,250ms cubic-bezier(0.73, 0.09, 0.21, 0.96) color,250ms cubic-bezier(0.73, 0.09, 0.21, 0.96) opacity}.button:focus{outline:0;box-shadow:0 0 1px 3px #eee}.button:active{box-shadow:none}.button:disabled{background:transparent;opacity:.5;cursor:not-allowed}.button:not(:disabled):hover{background:rgba(46,46,46,0.08);color:#555}.button.textNegative{color:#ff421e}.button.textNegative:hover{color:#ff421e}.icon{display:flex;flex-shrink:0;width:20px;height:20px;fill:currentColor}.icon svg{display:block;width:100%;height:100%}.input{width:40px;max-width:100%;padding:10px 10px;background-color:#f4f4f4;border:0;border-radius:10px;box-shadow:none;color:#2e2e2e;font-weight:600;text-align:center}.input::-webkit-input-placeholder{color:#888;font-weight:400;opacity:1}.input:-ms-input-placeholder{color:#888;font-weight:400;opacity:1}.input::placeholder{color:#888;font-weight:400;opacity:1}.input:focus{outline:0;box-shadow:0 0 1px 3px #eee}.input:disabled{opacity:.5;cursor:not-allowed}.pagination{display:flex;justify-content:start;align-items:center;padding:20px 0}.text{display:block}@media only screen and (max-width: 375px){.pageText{display:none}}.pageTitle{position:absolute;top:20px;left:20px;right:20px;text-align:center}.currentPageLabel{position:absolute;bottom:20px;left:20px}

.todo{margin-bottom:1rem}.todo.completed span{text-decoration:line-through}.todo label{color:#00e676;width:100%;display:flex;align-items:center;justify-content:space-between;cursor:pointer;padding:1rem;border-bottom:1px solid gray}.todo i{z-index:2}

